import React from 'react'
import "./projects.css"
import { motion } from "framer-motion"

const Project = () => {
    return (
        <>
        <motion.div 
        className="col-md-4 mb-4"
        initial={{ scale:0 }}
        animate={{ y: 10 , scale:1, }}
        whileHover={{ scale: 1.05, y: 10}}

        >
            
                <div className="card bg-light rounded">
                    <img src="https://media.geeksforgeeks.org/wp-content/cdn-uploads/20200203200336/13-Things-You-Should-Know-Before-You-Enter-In-Web-Development.png" class="card-img-top" alt="..." />
                    <div className="card-body">
                        <h3 className="card-title">Project title</h3>
                        <p className="card-text text-justify">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                        <h5>
                            Skills:
                            <a className="btn " href="">HTML</a>
                            <a className="btn" href="">CSS</a>
                            <a className="btn" href="">BOOTSTRAP</a>
                            <a className="btn" href="">JAVASCRIPT</a>
                            <a className="btn" href="">REACT.JS</a>
                        </h5>
                        <motion.a href="#" className="btn btn-primary btn-lg d-block"
                            whileHover={{ scale: 1.05 }}
                            whileTap={{ scale: 0.95 }}
                        >View Details
                        </motion.a>
                    </div>
                </div>
            
        </motion.div>
        </>
    )
}

export default Project
