import React from 'react'

import "./Navbar.css"

const Navbar = () => {
    return (
        <>
        <nav className="navbar navbar-expand-lg navbar-light ">
        <div className="container mt-4 py-2">
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#mobile-menu" aria-controls="mobile-menu" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="mobile-menu">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item mx-1">
                <a className="nav-link active" aria-current="page" href="/">Home</a>
                </li>
                <li className="nav-item mx-1">
                <a className="nav-link" href="#about">About us</a>
                </li>

                <li className="nav-item dropdown mx-1">
                <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Collection
                </a>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                    <li><a className="dropdown-item" href="#">My World Tour</a></li>
                </ul>
                </li>
                <li className="nav-item dropdown mx-1">
                <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Platforms
                </a>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                    <li><a className="dropdown-item" href="#">OpenSea.io</a></li>
                    {/* <li><a className="dropdown-item" href="#">Another action</a></li>
                    <li><hr className="dropdown-divider"/></li>
                    <li><a className="dropdown-item" href="#">Something else here</a></li> */}
                </ul>
                </li>
                
            </ul>

            <span className="d-flex ms-auto mb-2 mb-lg-0">
                    <a className="nav-link " aria-current="page" href="#">
                        <i className="bi bi-instagram"></i>
                    </a>
                    <a className="nav-link" aria-current="page" href="#">
                        <i className="bi bi-discord"></i>
                    </a>
                    <a className="nav-link" aria-current="page" href="#">
                        <i className="bi bi-twitter"></i>
                    </a>
            </span>
            </div>
        </div>
        </nav>
        </>
    )
}

export default Navbar
